.news {
  &__slide-header {
    margin-bottom: calc(var(--padding-and-margin) * 2);
  }
  &__slide-time {
    color: $text-news--time-color;
  }
  &__slide-img {
    width: 100%;
    margin-bottom: .9375rem;
    @include for-size(tablet-landscape-up) {
      width: auto;
      margin: 0 0.9375rem 0.9375rem 0;
      float: left;
    }
  }
  & .swiper-slide {
    opacity: 0 !important;
  }
  & .swiper-slide.swiper-slide-active {
    opacity: 1 !important;
  }
  & .swiper-container {
    padding-bottom: $padding-and-margin;
    @include for-size(phone-only) {
      padding-top: calc(var(--padding-and-margin) * 2);
      padding-bottom: 0;
      margin-top: -75px;
    }
  }
  & .swiper-button-next,
  & .swiper-button-prev {
    top: 100%;
    margin-top: -40px;
    width: auto;
    line-height: 45px;
    color: $color-primary;

    @include for-size(phone-only) {
      // opacity: 0;
      top: 0;
      margin-top: 0;
      font-size: 0;
    }
  }

  & .swiper-button-prev {
    position: absolute;
    left: 30%;
    padding-left: 45px;

    background-image: none;
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0%;
      transform: translate(0%, -50%) rotate(180deg);
      padding: 1.25rem;
      width: 20px;
      height: 10px;
      background-image: url("../img/icons/icon-menu_arrow-blue.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 50%;
      background-color: #ffffff;
      border-radius: 50%;
    }

    @include for-size(phone-only) {
      left: 70%;
    }
    @include for-size(tablet-portrait-up) {
      left: 0%;
    }
    @include for-size(desktop-up) {
      left: 30%;
    }
  }
  & .swiper-button-next {
    position: absolute;
    right: 30%;
    padding-right: 45px;

    background-image: none;
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      right: 0%;
      transform: translate(0%, -50%);
      padding: 1.25rem;
      width: 20px;
      height: 10px;
      background-image: url("../img/icons/icon-menu_arrow-blue.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 50%;
      background-color: #ffffff;
      border-radius: 50%;
    }
    &::after {
      content: "Назад в прошлое";
      position: absolute;
      right: 0;
      bottom: -40px;
      font-size: 80%;
      line-height: normal;
      text-align: right;
      color: $color-primary;
    }

    @include for-size(phone-only) {
      right: 0;
    }
    @include for-size(tablet-portrait-up) {
      right: 0%;
    }
    @include for-size(desktop-up) {
      right: 30%;
    }
  }
}
